.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.black-nav{
  display:flex;
  background: black;
  width:100%;
  color:white;
  padding-left:20px;
  align-items: center;
}
.list{
  border-bottom:1px solid black;
  text-align: left;
  padding:0 20px;
}
.App-link {
  color: #61dafb;
}
.modal{
  background: skyblue;
  text-align: left;
  padding:20px;
  
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
